button {
    border: none;
}
  
.submit-button {
    display: block;
    width: 100%;
    padding: 0.75rem;
    background: #235952;
    color: inherit;
    border-radius: 2rem;
    cursor: pointer;
}

.submit-button:hover {
    background: #687466;
}

label {
    display: flex;
    align-items: center;
    font-size: 1rem;
    margin-bottom: 0.5rem;
}

input, button, select, textarea {
    margin: 0;
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
    box-sizing:border-box
}

.input-radio, .input-checkbox {
    display: inline-block;
    margin-right: 0.625rem;
    min-height: 1.25rem;
    min-width: 1.25rem;
  }
  
.input-textarea {
    min-height: 120px;
    width: 100%;
    padding: 0.625rem;
    resize: vertical;
    resize: none;
}

.clue {
    margin-left: 0.25rem;
    font-size: 0.9rem;
    color: rgb(223,173,154);
}
  
form {
    background:rgb(36,43,43, 0.8);
    padding: 2.5rem 0.625rem;
    border-radius: 1rem;
}

.form-group {
    margin: 0 auto 1.25rem auto;
    padding: 0.25rem;
}
  
.form-control {
    display: block;
    width: 100%;
    height: 2.375rem;
    padding: 0.375rem 0.75rem;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: 0.25rem;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
  
.form-control:focus {
    border-color: rgb(223,173,154);
    outline: 0;
    box-shadow: 0 0 0 0.17rem rgb(181,136,133, 0.5);
}

  
@media (min-width: 480px) {
    form {
      padding: 2.5rem;
    }
}
